.loginPage {
  background-image: url("./../../public/assets/bg.jpg");
  /* width: 100vw; */
  height: calc(100vh - 80px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 100px;
  border-radius: 4px;
  box-shadow: 0 -2px 0 rgba(0, 0, 0, 0.04), 0 2px 25px rgb(141, 89, 41, 0.25);
}

.loginPage p {
  font-size: 25px;
  margin-bottom: 20px;
  opacity: 0.8;
}

/* GOOGLE BUTTOn */
.loginWithGoogle {
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
  padding: 5px 16px 12px 42px;
  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  color: rgb(117, 117, 117);
  font-size: 25px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;
}
.loginWithGoogle:hover {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25);
}
.loginWithGoogle:active {
  background-color: #eeeeee;
}
.loginWithGoogle:focus {
  outline: none;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25),
    0 0 0 3px #c8dafc;
}
.loginWithGoogle:disabled {
  filter: grayscale(100%);
  background-color: #ebebeb;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  cursor: not-allowed;
}
